import "../styles/index.scss";
import $ from "jquery";
import "popper.js/dist/popper.min";
import "node-snackbar/dist/snackbar.min.css";
import Snackbar from "node-snackbar";
// import  "node-snackbar/dist/snackbar.min";
import "bootstrap";
import Validator from "validatorjs";

// data url to load inventory data from
let url = "/data/inventory.json";

// Defining important dom elements for later use
let table = document.querySelector("#inventory-table");
let inventoryContainer = document.querySelector(".inventory");
let inventoryModal = document.querySelector("#inventoryModal");
let inventoryForm = document.querySelector("#item-form");
let loaderContainer = document.querySelector(".loader-container");
let deleteConfirmation = document.querySelector("#delete-confirmation");
let itemSaveBtn = document.querySelector("#item-save");

// Assets loaded, removing loader and showing the page content
$(".row.loader").hide();
$("#main-content").show();

// Fetching inventory data from url
fetch(url)
	.then((response) => response.json())
	.then((jsonData) => {
		// Generating table body with received data
		let tableTemplate = jsonData.reduce((acc, item, idx) => {
			return (
				acc +
				`
                <tr>
                    <td>${idx + 1}</td>
                    <td>${item.id}</td>
                    <td>${item.name}</td>
                    <td>${item.type}</td>
                    <td>${item.cost}</td>
                    <td>${item.quantity}</td>
                    <td>
						<div class="btn-group" role="group" aria-label="Actions">
							<button type="button" class="btn btn-primary" data-toggle="modal" data-target="#inventoryModal" data-id="${item.id}">
								Edit
							</button>
							<button type="button" class="btn btn-danger" data-toggle="modal" data-target="#deleteAlertModal" data-id="${item.id}">
								Delete
							</button>
                        </div>
                    </td>
                </tr>
            `
			);
		}, "");

		// Inserting generated table template to table
		table.querySelector("tbody").innerHTML = tableTemplate;

		// Hiding loader and showing updated table
		$(loaderContainer).hide();
		$(table).show();

		$(inventoryModal).on("show.bs.modal", function (event) {
			let button = $(event.relatedTarget); // Button that triggered the modal
			let id = button.data("id"); // Extract info from data-* attributes

			// Find item from data set to null if not found
			let item = jsonData.filter((item) => item.id === id)[0] || null;

			let modal = $(this);

			if (item) {
				// Item found sending edit form
				modal.find(".modal-title").text(`Edit: ${item.name}`);
				["name", "cost", "quantity", "type"].forEach((attribute) => {
					modal
						.find(`.modal-body [name=${attribute}]`)
						.removeClass("is-invalid")
						.removeClass("is-valid")
						.val(item[attribute]);
				});
			} else {
				// Item not found sending create form. Cleaning up the form with empty data
				modal.find(".modal-title").text(`Create new item`);
				["name", "cost", "quantity", "type"].forEach((attribute) => {
					modal
						.find(`.modal-body [name=${attribute}]`)
						.removeClass("is-invalid")
						.removeClass("is-valid")
						.val("");
				});
			}
		});

		$(deleteAlertModal).on("show.bs.modal", function (event) {
			let button = $(event.relatedTarget);
			let id = button.data("id");
			$(deleteConfirmation).data("id", id);
		});

		$(deleteConfirmation).on("click", function () {
			let itemId = $(deleteConfirmation).data("id");

			$.ajax("#deleteItem", {
				type: "get", // will usually have POST here, but I am putting GET here just to get success on response
				data: { id: itemId },
				success: function (data, status) {
					if (status === "success") {
						Snackbar.show({ text: `Inventory item with id "${itemId}" successfully deleted.` });
						$(deleteAlertModal).modal("hide");
					}
				},
				error: function (jqXhr, textStatus, errorMessage) {
					console.log(jqXhr, textStatus, errorMessage);
				},
			});
		});

		$(itemSaveBtn).on("click", function () {
			let formData = _get_values(inventoryForm);

			let validation = new Validator(formData, {
				name: ["required", "between:3,25"],
				type: ["required", "in:Television,Headphone,Smartphone,Computer"],
				cost: ["required", "regex:/^(?!0\\.00)\\d{1,10}(,\\d{10})*(\\.\\d{1,2})?$/"],
				quantity: ["required", "numeric", "between:0,9999"],
			});

			if (validation.fails()) {
				["name", "cost", "quantity", "type"].forEach((attribute) => {
					updateFormValidation(attribute, validation.errors.first(attribute));
				});
			} else {
				$.ajax("#saveItem", {
					type: "get", // will usually have POST here, but I am putting GET here just to get success on response
					data: formData,
					success: function (data, status) {
						if (status === "success") {
							Snackbar.show({ text: `Inventory item "${formData.name}" successfully updated` });
							$(inventoryModal).modal("hide");
						}
					},
					error: function (jqXhr, textStatus, errorMessage) {
						console.log(jqXhr, textStatus, errorMessage);
					},
				});
			}
		});
	});

function updateFormValidation(attribute, error) {
	if (error) {
		$(inventoryForm)
			.find(`.form-group.group-${attribute} [name=${attribute}]`)
			.addClass("is-invalid")
			.removeClass("is-valid");
		$(inventoryForm).find(`.form-group.group-${attribute} .invalid-feedback`).text(error);
	} else {
		$(inventoryForm)
			.find(`.form-group.group-${attribute} [name=${attribute}]`)
			.addClass("is-valid")
			.removeClass("is-invalid");
	}
}

// function to get values of a form as an object
function _get_values(form) {
	let data = {};
	$(form)
		.find("input, textarea, select")
		.each(function (x, field) {
			if (field.name) {
				if (field.name.indexOf("[]") > 0) {
					if (!$.isArray(data[field.name])) {
						data[field.name] = new Array();
					}
					for (let i = 0; i < field.selectedOptions.length; i++) {
						data[field.name].push(field.selectedOptions[i].value);
					}
				} else {
					data[field.name] = field.value;
				}
			}
		});
	return data;
}
